import "core-js/modules/es.array.push.js";
import { ElButton, ElMessage, ElInput, ElSelect, ElOption, ElCheckbox, ElCheckboxGroup } from "element-plus";
export default {
  components: {
    ElButton,
    ElInput,
    ElSelect,
    ElOption,
    ElCheckbox,
    ElCheckboxGroup
  },
  data() {
    return {
      loading: false,
      // 患儿基本信息（新增年龄分层、喂养方式、体质类型）
      patientInfo: {
        name: '',
        gender: '',
        ageLayer: '',
        // 年龄分层（儿科核心分类）
        age: '',
        feedingWay: '',
        // 喂养方式
        constitution: '' // 体质类型
      },

      // 症状信息（替换外科病变为儿科核心症状）
      symptoms: {
        general: [],
        // 全身症状
        system: [],
        // 系统症状
        daily: [],
        // 饮食睡眠二便
        otherSymptoms: '' // 其他症状
      },

      // 发病情况（新增疫苗接种史、接触史）
      onset: {
        situation: '',
        // 发病特点
        duration: '',
        // 持续时间
        vaccineStatus: '',
        // 疫苗接种史
        factors: [],
        // 可能诱因
        contactHistory: '',
        // 接触史
        otherFactors: '' // 其他诱因
      },

      // 诊疗经过（保留原结构）
      treatment: {
        previousDiagnosis: '',
        // 既往诊断
        previousTreatment: '',
        // 曾接受的治疗
        treatmentEffect: '' // 治疗效果
      },

      // 全身症状（儿科重点：发热、精神状态等）
      generalSymptoms: [{
        label: '发热（体温≥37.5℃）',
        value: '发热'
      }, {
        label: '精神差（嗜睡、烦躁）',
        value: '精神差'
      }, {
        label: '哭闹不安（无明显诱因）',
        value: '哭闹不安'
      }, {
        label: '抽搐（惊厥、肢体抽动）',
        value: '抽搐'
      }, {
        label: '面色异常（苍白、发黄、潮红）',
        value: '面色异常'
      }, {
        label: '出汗多（盗汗、自汗）',
        value: '出汗多'
      }, {
        label: '体重下降（近期明显）',
        value: '体重下降'
      }],
      // 系统症状（儿科常见：呼吸、消化、皮肤等）
      systemSymptoms: [{
        label: '咳嗽（干咳/有痰）',
        value: '咳嗽'
      }, {
        label: '喘息（呼吸急促、喉鸣）',
        value: '喘息'
      }, {
        label: '咽痛（拒食、流涎）',
        value: '咽痛'
      }, {
        label: '呕吐（喷射性/非喷射性）',
        value: '呕吐'
      }, {
        label: '腹泻（大便次数增多、稀便）',
        value: '腹泻'
      }, {
        label: '便秘（大便干结、排便困难）',
        value: '便秘'
      }, {
        label: '腹痛（腹部拒按、蜷缩）',
        value: '腹痛'
      }, {
        label: '皮疹（湿疹、荨麻疹、疱疹）',
        value: '皮疹'
      }, {
        label: '黄疸（皮肤、眼白发黄）',
        value: '黄疸'
      }, {
        label: '尿频尿急（排尿哭闹）',
        value: '尿频尿急'
      }],
      // 饮食睡眠二便（儿科特色评估项）
      dailySymptoms: [{
        label: '食欲下降（拒奶、拒食）',
        value: '食欲下降'
      }, {
        label: '挑食偏食（只吃单一食物）',
        value: '挑食偏食'
      }, {
        label: '睡眠不安（易醒、多梦）',
        value: '睡眠不安'
      }, {
        label: '盗汗（夜间出汗多）',
        value: '盗汗'
      }, {
        label: '大便稀溏（每日≥3次）',
        value: '大便稀溏'
      }, {
        label: '大便干结（3天以上未排便）',
        value: '大便干结'
      }, {
        label: '小便黄少（尿量减少、颜色深）',
        value: '小便黄少'
      }, {
        label: '口臭（口腔异味明显）',
        value: '口臭'
      }, {
        label: '流口水（非出牙期明显）',
        value: '流口水'
      }],
      // 发病诱因（儿科常见：受凉、饮食、接触等）
      onsetFactors: [{
        label: '受凉（吹风、空调直吹）',
        value: '受凉'
      }, {
        label: '饮食不当（过饱、生冷食物）',
        value: '饮食不当'
      }, {
        label: '接触传染病患者（感冒、手足口等）',
        value: '接触传染病患者'
      }, {
        label: '环境变化（外出、换新环境）',
        value: '环境变化'
      }, {
        label: '过敏刺激（接触花粉、宠物）',
        value: '过敏刺激'
      }, {
        label: '疫苗接种后反应',
        value: '疫苗接种后反应'
      }, {
        label: '劳累（活动过多、睡眠不足）',
        value: '劳累'
      }, {
        label: '不明原因',
        value: '不明原因'
      }]
    };
  },
  methods: {
    // 年龄分层变化时的联动提示（如新生儿特殊注意事项）
    handleAgeLayerChange(val) {
      if (val === '新生儿') {
        ElMessage.info('新生儿症状变化快，建议结合专业医生诊断');
      }
    },
    // 验证输入信息（强化儿科必填项：年龄分层、核心症状）
    validateInput() {
      // 必选验证：年龄分层（儿科诊疗核心依据）
      if (!this.patientInfo.ageLayer) {
        ElMessage.warning('请选择患儿年龄分层（如：婴儿、幼儿）');
        return false;
      }

      // 必选验证：至少选择1项核心症状
      if (this.symptoms.general.length === 0 && this.symptoms.system.length === 0 && this.symptoms.daily.length === 0 && !this.symptoms.otherSymptoms) {
        ElMessage.warning('请至少提供1项患儿症状信息（如：发热、咳嗽）');
        return false;
      }

      // 特殊验证：新生儿需填写具体日龄
      if (this.patientInfo.ageLayer === '新生儿' && !this.patientInfo.age) {
        ElMessage.warning('新生儿请填写具体日龄（如：15天）');
        return false;
      }
      return true;
    },
    // 组合所有输入的数据（适配儿科信息结构）
    combineData() {
      let data = [];

      // 添加患儿基本信息
      data.push("患儿基本信息：");
      if (this.patientInfo.name) data.push(`姓名：${this.patientInfo.name}`);
      if (this.patientInfo.gender) data.push(`性别：${this.patientInfo.gender}`);
      if (this.patientInfo.ageLayer) data.push(`年龄分层：${this.patientInfo.ageLayer}`);
      if (this.patientInfo.age) data.push(`具体年龄：${this.patientInfo.age}`);
      if (this.patientInfo.feedingWay) data.push(`喂养方式：${this.patientInfo.feedingWay}`);
      if (this.patientInfo.constitution) data.push(`体质类型：${this.patientInfo.constitution}`);
      data.push("");

      // 添加症状信息
      data.push("核心症状表现：");
      if (this.symptoms.general.length > 0) {
        data.push(`全身症状：${this.symptoms.general.join('、')}`);
      }
      if (this.symptoms.system.length > 0) {
        data.push(`系统症状：${this.symptoms.system.join('、')}`);
      }
      if (this.symptoms.daily.length > 0) {
        data.push(`饮食睡眠二便：${this.symptoms.daily.join('、')}`);
      }
      if (this.symptoms.otherSymptoms) {
        data.push(`其他症状：${this.symptoms.otherSymptoms}`);
      }
      data.push("");

      // 添加发病情况
      data.push("发病与接触史：");
      if (this.onset.situation) data.push(`发病特点：${this.onset.situation}`);
      if (this.onset.duration) data.push(`持续时间：${this.onset.duration}`);
      if (this.onset.vaccineStatus) data.push(`疫苗接种史：${this.onset.vaccineStatus}`);
      if (this.onset.factors.length > 0) {
        data.push(`可能诱因：${this.onset.factors.join('、')}`);
      }
      if (this.onset.contactHistory) {
        data.push(`接触史：${this.onset.contactHistory}`);
      }
      if (this.onset.otherFactors) {
        data.push(`其他诱因：${this.onset.otherFactors}`);
      }
      data.push("");

      // 添加诊疗经过
      if (this.treatment.previousDiagnosis || this.treatment.previousTreatment || this.treatment.treatmentEffect) {
        data.push("诊疗与用药史：");
        if (this.treatment.previousDiagnosis) data.push(`既往诊断：${this.treatment.previousDiagnosis}`);
        if (this.treatment.previousTreatment) data.push(`曾接受的治疗：${this.treatment.previousTreatment}`);
        if (this.treatment.treatmentEffect) data.push(`治疗效果：${this.treatment.treatmentEffect}`);
      }
      return data.join("\n");
    },
    // 生成分析（保持原有核心功能不变）
    async generateAnalysis() {
      // 验证输入
      if (!this.validateInput()) {
        return;
      }
      this.loading = true;
      try {
        // 组合数据
        const patientData = this.combineData();
        // 构建提示信息
        let prompt = "我需要你使用中医儿科的知识对患儿症状进行辨证分析，这是信息：\n";
        let msg = prompt + patientData;
        const encodedDescription = encodeURIComponent(msg);

        // 尝试导入数据传输工具
        import('@/lin/util/data-transfer').then(({
          saveData,
          generateUniqueId
        }) => {
          // 生成唯一ID
          const dataId = generateUniqueId();
          let infos = msg;
          // 存储数据到localStorage
          const saved = saveData(dataId, infos);
          console.log(dataId);
          if (saved) {
            // 传递数据ID作为参数
            try {
              const newWindow = window.open(`/#/chat?data_id=${dataId}`);
              if (!newWindow || newWindow.closed || typeof newWindow.closed === 'undefined') {
                // 如果window.open失败，使用location.href
                window.location.href = `/#/chat?data_id=${dataId}`;
              }
            } catch (error) {
              // 捕获任何可能的错误，使用location.href作为后备方案
              window.location.href = `/#/chat?data_id=${dataId}`;
            }
          }
        }).catch(error => {
          console.error('加载数据传输工具失败:', error);
          // 如果导入失败，回退到原来的方式
          window.location.href = `/#/chat?user_input=${encodedDescription}`;
        });
      } catch (error) {
        ElMessage.error('生成分析信息时出错');
        console.error(error);
      } finally {
        this.loading = false;
      }
    }
  }
};